<template>
	<view>
		<Bar></Bar>
		<view class="content-head"
			<!-- #ifdef MP-WEIXIN -->
				:style="{
					<!-- height: -->
					height:$getSystemInfoSync().height + 'px',
					paddingTop:$getSystemInfoSync().statusBarHeight+'px',
					background:backColor
				}"
			<!-- #endif -->
			
			<!-- #ifdef H5-->
				:style="{
					height:h5Height,
					background:backColor
				}"
			<!-- #endif -->
		>
			<slot></slot>
		</view>
	</view>
</template>

<script>
	export default {
		props:{
			backColor:{
				type:String,
				default:"#ffffff"
			},
			"h5Height":{
				type:String,
				default:"100rpx"
			}
		}
	}
	
	
</script>

<style lang="scss">
	.content-head{
		top: 0;
		position: fixed;
		background: $uni-bg-color;
		z-index: 10000;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		height: 100rpx;
		margin-bottom:10px;			
	}
</style>
